*{margin:0;padding:0}
ul,ol{list-style: none;}
.todo-list{
  width: 550px;
  margin:130px auto;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2), 0 25px 50px 0 rgba(0,0,0,0.1);

}
.control h1{
  width: 100%;
  font-size: 100px;
  text-align: center;
  color:rgba(175,47,47,0.15)
}
.control .new-todo{
  padding:16px 16px 16px 60px;
  border:0;
  outline:none;
  font-size:24px;
  box-sizing: border-box;
  width: 100%;
  line-height: 1.4rem;
  box-shadow: inset 0 -2px 1px rgba(0,0,0,0.3);
  
}

.todo-item{
  font-size: 24px;
  display: flex;
  align-items: center;
}
.todo-item input{
  display: block;
  width: 20px;
  height: 20px;
  margin:0 20px
}

.todo-item label{
  flex:1;
  padding:15px 15px 15px 0;
  line-height: 1.2;
  display: block;
}

.todo-item label.complete{
  text-decoration: line-through;
}
.todo-item button{
  border:0;
  outline: 0;
  display: block;
  text-align: center;
  font-size: 30px;
  color:#cc9a9a;
  background: #fff;
}

















